import { ContextApi } from 'contexts/Localization/types'
import { PageMeta } from './types'

export const DEFAULT_META: PageMeta = {
  title: 'MasterMind Games',
  description:
    'The most popular AMM on BSC by user count! Earn MGT through yield farming or win it in the Lottery, then stake it in Syrup Pools to earn more tokens! Initial Farm Offerings (new token launch model pioneered by MasterMind Games), NFTs, and more, on a platform you can trust.',
  image: ' ',
}

export const getCustomMeta = (path: string, t: ContextApi['t']): PageMeta => {
  let basePath
  if (path.startsWith('/swap')) {
    basePath = '/swap'
  } else if (path.startsWith('/add')) {
    basePath = '/add'
  } else if (path.startsWith('/remove')) {
    basePath = '/remove'
  } else if (path.startsWith('/teams')) {
    basePath = '/teams'
  } else if (path.startsWith('/voting/proposal') && path !== '/voting/proposal/create') {
    basePath = '/voting/proposal'
  } else {
    basePath = path
  }

  switch (basePath) {
    case '/':
      return {
        title: `${t('Home')} | ${t('MasterMind Games')}`,
      }
    case '/swap':
      return {
        title: `${t('Exchange')} | ${t('MasterMind Games')}`,
      }
    case '/add':
      return {
        title: `${t('Add Liquidity')} | ${t('MasterMind Games')}`,
      }
    case '/remove':
      return {
        title: `${t('Remove Liquidity')} | ${t('MasterMind Games')}`,
      }
    case '/liquidity':
      return {
        title: `${t('Liquidity')} | ${t('MasterMind Games')}`,
      }
    case '/find':
      return {
        title: `${t('Import Pool')} | ${t('MasterMind Games')}`,
      }
    case '/competition':
      return {
        title: `${t('Trading Battle')} | ${t('MasterMind Games')}`,
      }
    case '/prediction':
      return {
        title: `${t('Prediction')} | ${t('MasterMind Games')}`,
      }
    case '/prediction/leaderboard':
      return {
        title: `${t('Leaderboard')} | ${t('MasterMind Games')}`,
      }
    case '/farms':
      return {
        title: `${t('Farms')} | ${t('MasterMind Games')}`,
      }
    case '/farms/auction':
      return {
        title: `${t('Farm Auctions')} | ${t('MasterMind Games')}`,
      }
    case '/pools':
      return {
        title: `${t('Pools')} | ${t('MasterMind Games')}`,
      }
    case '/lottery':
      return {
        title: `${t('Lottery')} | ${t('MasterMind Games')}`,
      }
    case '/collectibles':
      return {
        title: `${t('Collectibles')} | ${t('MasterMind Games')}`,
      }
    case '/ifo':
      return {
        title: `${t('Initial Farm Offering')} | ${t('MasterMind Games')}`,
      }
    case '/teams':
      return {
        title: `${t('Leaderboard')} | ${t('MasterMind Games')}`,
      }
    case '/profile':
      return {
        title: `${t('Your Profile')} | ${t('MasterMind Games')}`,
      }
    case '/profile/tasks':
      return {
        title: `${t('Task Center')} | ${t('MasterMind Games')}`,
      }
    case '/voting':
      return {
        title: `${t('Voting')} | ${t('MasterMind Games')}`,
      }
    case '/voting/proposal':
      return {
        title: `${t('Proposals')} | ${t('MasterMind Games')}`,
      }
    case '/voting/proposal/create':
      return {
        title: `${t('Make a Proposal')} | ${t('MasterMind Games')}`,
      }
    case '/info':
      return {
        title: `${t('Overview')} | ${t('MasterMind Games Info & Analytics')}`,
        description: 'View statistics for MasterMind Games exchanges.',
      }
    case '/info/pools':
      return {
        title: `${t('Pools')} | ${t('MasterMind Games Info & Analytics')}`,
        description: 'View statistics for MasterMind Games exchanges.',
      }
    case '/info/tokens':
      return {
        title: `${t('Pools')} | ${t('MasterMind Games Info & Analytics')}`,
        description: 'View statistics for MasterMind Games exchanges.',
      }
    default:
      return null
  }
}
